@import url(https://fonts.googleapis.com/css?family=Roboto:300, 400, 700);
@import url(https://cdn.cloudcache.dk/materialize/0.95.3/css/materialize-fonts.css);
@import url(https://fonts.googleapis.com/css?family=Lobster&display=swap);
.header-home {
    width: 100%;
    text-align: center;
    background-image: url(/static/media/bg.e2e9a886.svg);
    color: #eee;
    padding: 200px 0;
}
.header-home > h1 {
    font-weight: 400;
    font-size: 64px;
}

.section-botoes {
    margin: 5%;
}

.button-bora {
    margin: 2%;
}

.input-home {
    width: 70% !important;
    border-radius: 20px !important;
    padding: 1% !important;
    height: 0% !important;
}
.titulo-home {
    font-family: "Lobster", cursive;
}

.color-titulo-home {
    font-size: 1.5em;
    font-weight: 500;
    margin: 1%;
    -webkit-text-stroke-width: 0.4px;
    -webkit-text-stroke-color: #000;
    color: #a188ff;
    display: flex;
    justify-content: flex-start;
}

